.portfolio-detail-description-wrapper {
    text-align: center;
    margin-top: 42px;
    display: flex;
    justify-content: center;
  
    .description {
      width: 700px;
      font-size: 2em;
      margin-bottom: 50px;
    }
  }

  .bottom-content-wrapper {
    width: 100%;
    display: flex;
    justify-content: center;
  }
  .site-link {
    padding: 15px;
    background-color: $teal;
    border-radius: 3px;
    text-decoration: none;
    color: white;
    transition: 0.5s ease-in-out;
    font-size: 1.2em;
  
    &:hover {
      background-color: $dark-teal;
    }
  }